#cover {
    display: flex;
    margin: 0;
    padding: 1vmin;
    height: 100vh;
    width: 100%;
    justify-content: center;
    box-sizing: border-box;
    animation: showCover 2s ease-out;
}

@keyframes showCover {
    0% {
        opacity: 0;
        // width: fit-content;
    }
    50% {
        opacity: 0.5;
        // width: fit-content;
    }
    100% {
        opacity: 1;
        // width: fit-content;
    }
}

#cover:fullscreen {
    height: 100vh;
}

.column {
    display: flex;
    flex-direction: column;
    margin: 0 3vmin;
    align-items: center;
}

#center-page {
    width: 50vmin;
    justify-content: center;
}

#my-name {
    width: 4vw;
    justify-content: space-evenly;
}

#my-name, #menu, #center-page {
    height: 90vh;
}

#first-name,
#last-name {
    color: #7db08c;
    font-size: 5vmin;
    font-family: "Unlock", cursive;
    opacity: 0.7;
    text-transform: uppercase;
    transition: all 2s ease-out;
}

#first-name {
    text-shadow: -6px -4px 4px rgb(15, 48, 60);
}

#last-name {
    text-shadow: -6px 4px 4px rgb(11, 35, 43);
}

#profile-pic {
    background: radial-gradient(rgba(147, 233, 190, 0.1) 0%, rgba(34, 167, 101, 0.9) 100%), url("../assets/p - 270 - repeat.jpg");
    background-size: contain;
    border-top: 1px solid rgb(0, 120, 240);
    border-bottom: 1px solid rgb(0, 120, 240);
    border-left: 6px solid rgb(0, 120, 240);
    border-right: 6px solid rgb(0, 120, 240);
    background-position: center;
    border-radius: 100%;
    box-shadow: -9px 9px 36px #61C2A2, 9px -9px 36px #61C2A2;
    width: 30vmin;
    height: 30vmin;
    align-self: center;
    justify-content: center;
    transition: all 1s ease-out;
}

#profile-pic:hover {
    cursor: pointer;
    box-shadow: 0 27px 45px -9px #0eaee8;
}

#profile-pic:active {
    cursor: context-menu;
    opacity: 0;
}

#menu {
    justify-content: space-evenly;
    align-items: center;
}

.menu-item {
    display: flex;
    color: unset;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: "Oswald", sans-serif;
    font-size: 2.5vmin;
    background: radial-gradient(rgba(94, 155, 113, 0.8) 0%, rgba(204, 224, 210, 0.8) 100%);
    border: unset;
    border-bottom: 4px solid rgb(0, 120, 240);
    border-left: 2px solid rgb(0, 120, 240);
    border-right: 2px solid rgb(0, 120, 240);
    border-radius: 100%;
    box-shadow: 2px 2px 99px #61C2A2;
    width: 7.5vmin;
    height: 7.5vmin;
    transition: all 300ms ease;
}

.menu-item:hover {
    cursor: pointer;
    background: radial-gradient(rgba(94, 155, 113, 1) 0%, rgba(204, 224, 210, 1) 100%);
    transform: scale(1.4);
    // font-size: larger;
}

.menu-item:active {
    cursor: context-menu;
    transform: scale(0.8);
}

.title {
    font-size: 5vmin;
    text-shadow: -2px 2px 4px #61C2A2;
    display: flex;
    justify-content: center;
}

#content {
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    font-family: "Montserrat", sans-serif;
    color: #CCE0D2;
    display: flex;
    flex-direction: column;
    transition: all 2s ease-in;
    text-align: center;
    animation: openOpacity 1s ease-out;
    line-height: 2.5vmin;
}

.intro-p, .link-item, .content-item {
    animation: openOpacity 2s ease-out;
    font-size: 2.5vmin;
}

@keyframes openOpacity {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}

.content-item {
    color: unset;
    display: flex;
    text-shadow: -1px 2px 3px #61C2A2;
    margin: 0 1rem;
    align-self: center;
}

#content::-webkit-scrollbar {
    display: none;
}

.intro:hover {
    cursor: grab;
}

.link-item {
    color: unset;
    display: flex;
    transition: all 0.5s ease;
    align-self: center;
}

.link-item:hover {
    text-shadow: 3px 3px 7px #61C2A2, -3px -3px 7px #61C2A2;
    font-weight: bold;
}

.contact-logo {
    border: 1px solid rgb(132, 254, 148);
    padding: 0.6rem 1.4rem;
    height: 5vmin;
    display: flex;
    justify-items: center;
    border-radius: 4px;
    box-shadow: 1px 1px 4px inset rgb(132, 254, 148);
    background: linear-gradient(25deg, rgba(87, 167, 98, 0.4) 25%, rgba(87, 167, 98, 0.1));
    transition: all 1s ease;
}

.contact-logo:hover {
    box-shadow: 3px 3px 7px #61C2A2, -3px -3px 7px #61C2A2;
}

#email-address:hover #email-address-tip{
    visibility: visible;
}

.mailtoui {
    position: relative;
}

.modalOpen {
    animation: openModal 0.5s ease-out forwards;
}

@keyframes openModal {
    0% {
        opacity: 0;
        transform: translateY(-100%);
    }
    50% {
        opacity: 1;
        transform: translateY(20%);
    }
    100% {
        opacity: 1;
        transform: translateY(0%);
    }
}

