.signIn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  animation: dissolve 1s ease-out;
}

@keyframes dissolve {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 50%;
  }
  100% {
    opacity: 100%;
  }
}
.signIn-input {
  background: linear-gradient(rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.7));
  border-bottom: 3px inset white;
  border-radius: 6px;
  padding: 10px 5px;
}

.signIn-input::placeholder {
  color: black;
  font-size: 2vmin;
}

.signIn-input, .signInButton {
  width: 18vmin;
}

.signInButton {
  padding: 5px 7px;
}

