.fuel-depot {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(30deg, rgb(200, 21, 21) 25%, rgb(95, 2, 2) 75%);
  overflow-x: hidden;
  animation: showCover 1s ease-out;
}

@keyframes showCover {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.fuel-title {
  display: grid;
  font-family: "Unlock", cursive;
  font-size: 4vw;
  justify-self: center;
  align-self: center;
  color: rgba(218, 125, 3, 0.7);
  text-transform: uppercase;
  text-shadow: -1px 1px 2px rgba(255, 96, 4, 0.7), 1px -1px 2px rgba(255, 96, 4, 0.7);
}

.type-symbol {
  width: 3vmin;
  display: inline-flex;
  align-self: center;
  background: linear-gradient(rgba(255, 128, 128, 0.4) 0%, rgba(255, 148, 148, 0.7) 100%);
  padding: 6px;
  border: 2px inset rgba(255, 145, 0, 0.7);
  border-radius: 6px;
  margin: auto 1vmin;
}

#fuel-in {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 1vh auto;
}

.fuel-input-group {
  display: flex;
  align-items: center;
}

.fuel-input {
  background: linear-gradient(rgba(200, 21, 30, 0.9) 25%, rgba(146, 7, 2, 0.3) 75%);
  box-shadow: 1px 1px 9px rgba(219, 23, 33, 0.9);
  outline: double 1px rgb(255, 96, 96);
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  border-bottom: 3px solid rgb(254, 130, 130);
  padding: 0.5rem 0.9rem;
  color: white;
}

.fuel-input, .rando-button {
  margin: 0 1vmin;
}

.fuel-input::placeholder {
  color: white;
}

.output-button {
  border: unset;
  justify-self: center;
  align-self: center;
  border-radius: 0.3rem;
  margin: 0 1vmin;
  padding: 1vmin;
  background: radial-gradient(rgba(146, 7, 2, 0.2) 30%, rgba(200, 21, 30, 0.7) 90%);
  box-shadow: 1px 1px 6px rgba(219, 23, 33, 0.9);
  color: white;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  border-bottom: 3px solid rgb(254, 130, 130);
  border-top: 3px solid rgb(254, 130, 130);
  transition: all 300ms ease-out;
}

.output-button, .fuel-input {
  font-size: 2vmin;
  width: 14vmin;
}

.output-button:active {
  scale: 0.9;
  background: radial-gradient(30deg, rgba(146, 7, 2, 0.6) 15%, rgb(200, 21, 30) 60%);
  box-shadow: -2px 2px 27px white, 2px -2px 27px white;
}

.fuelout {
  display: inline-flex;
  flex-wrap: wrap;
  width: 90vw;
  margin: 0.9rem auto;
  overflow: scroll;
  overflow-x: hidden;
  -ms-overflow-style: none;
  scrollbar-width: none;
  justify-content: center;
  color: rgb(255, 187, 0);
  font-family: "Unlock", cursive;
  font-size: 3vmin;
  line-height: 3vmin;
}

.fuelout::-webkit-scrollbar {
  display: none;
}

.output-button:hover {
  cursor: pointer;
  background: radial-gradient(30deg, rgba(146, 7, 2, 0.6) 15%, rgb(200, 21, 30) 60%);
  text-shadow: -6px 6px 27px white, 6px -6px 27px white;
}

.ingredientMeal {
  padding: 0 2rem 2rem;
}

.randoMeal {
  animation: showCover 1s ease;
}

.default, .randoMeal, .ingredientMeal {
  background: radial-gradient(rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.3) 25%, rgba(0, 0, 0, 0.3) 45%, rgba(0, 0, 0, 0) 100%);
  border-left: 3px ridge rgba(192, 135, 105, 0.3);
  border-right: 3px ridge rgba(200, 84, 21, 0.3);
}

#randoMeal-instructions {
  display: flex;
  flex-direction: column;
}

.randoMeal-instruction {
  padding: 0 2rem;
  margin-bottom: 2vmin;
}

#randoMeal-name, #randoMeal-area-category {
  display: flex;
  justify-content: center;
}

.final-inspirational {
  display: flex;
  flex-wrap: wrap;
  color: rgb(212, 157, 16);
  text-shadow: -1px 1px 1px rgba(255, 96, 4, 0.7), 1px -1px 1px rgba(255, 96, 4, 0.7);
  font-family: "Shrikhand", cursive;
  padding-left: 66vw;
  font-size: 3vmin;
  animation: dissolve 1s ease-out;
}

.ingredientMealItem {
  animation: dissolve 1s ease-out;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@keyframes dissolve {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
    transform: translateY(20%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}
.meal-img {
  padding: 6px 18px;
  outline: 2px dashed rgb(165, 31, 31);
  height: 50vh;
  justify-self: center;
}

.dish-title {
  display: grid;
  justify-content: center;
}

.mineral {
  background: linear-gradient(rgba(207, 108, 108, 0.3), rgba(206, 73, 73, 0.5));
  padding: 2vmin;
  border-bottom-right-radius: 1vmin;
  border-bottom-left-radius: 1vmin;
  font-size: 4vmin;
}

.dosage {
  padding-left: 4vmin;
}

.fuel-source {
  display: flex;
  flex-direction: column;
  background: linear-gradient(rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0.1) 45%, rgba(0, 0, 0, 0.1) 65%, rgba(0, 0, 0, 0.2) 100%);
  font-family: "Unlock", cursive;
  animation: dissolve 1s ease-out;
}

.fuel-sources-title {
  text-decoration: underline;
  font-size: 4vmin;
  display: flex;
  justify-content: center;
}

.fuel-sources-title, .mineral {
  position: sticky;
}

.randoMeal {
  animation: dissolve 1s ease-out;
}

.fuel-source-list {
  padding-left: 0;
  margin-left: 0;
}

