$numCols: 6;
$col-width: 100/$numCols;

.errands {
    display: flex;
    flex-direction: column;
    justify-content: center;
    // background: linear-gradient(rgb(2, 12, 68), rgb(9, 42, 228));
    background: linear-gradient(rgb(2, 12, 68), rgb(4, 19, 104));
    height: 100vh;
    overflow: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
    animation: showCover 1s ease-out;
}

.errands::-webkit-scrollbar {
    display: none;
}

.errands-title {
    justify-self: center;
    color: #A9C9FF;
    text-shadow: 1px -2px 6px rgba(255,255,255,0.7);
    text-transform: uppercase;
    font-family: 'Kenia', cursive;
    font-size: 8vmin;
    padding: 0;
    margin: 1vmin auto;
    text-stroke: 1px #4c5a72;
    -webkit-text-stroke: 1px #4c5a72;
    align-self: start;
}

.errand-category {
    text-transform: uppercase;
    font-family: 'McLaren', cursive;
    cursor: ns-resize;
    font-size: 3vmin;
    text-shadow:  1px -2px 6px rgba(255,255,255,0.7), -1px 2px 6px rgba(255,255,255,0.7);
    margin: 2vmin auto;
    color: white;
}

#add-errand-button {
    background: linear-gradient(rgba(255, 255, 255, 0.4) 60%, rgba(255, 255, 255, 0.2));
    border-radius: 3px;
    font-weight: bold;
    font-size: 2vmin;
    align-self: center;
    margin: auto 1vmin;
    padding: 10px 15px;
    border: 3px ridge white;
    color: white;
    transition: all 1.5s ease;
}

#add-errand-button:hover {
    cursor: pointer;
    background: linear-gradient(rgba(255, 255, 255, 0.6) 60%, rgba(255, 255, 255, 0.4));
}

#add-errand-button:active {
    scale: calc(0.9);
}

.errand-input {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    // background: linear-gradient(20deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.1));
    background: unset;
    border-bottom: 3px solid white;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    color: white;
}

#errand-input {
    margin: 1vmin auto;
}

.errand-input::placeholder {
    color: white;
    // font-family: 'Kenia', cursive;
    // font-family: 'Kanit', sans-serif;
    font-size: 3vmin;
}

input[type=date] {
    cursor: pointer;
    font-size: 3vmin;
    padding: unset;
    font-family: 'Kanit', sans-serif;
    text-align: right;
    color: white;
}

::-webkit-calendar-picker-indicator {
    filter: invert(1);
}

.inp {
    display: flex;
    grid-row: 2;
    justify-content: center;
    text-align: center;
    border-top-right-radius: 3vw;
    border-top-left-radius: 3vw;
}

#errand-task {
    width: 36vmin;
    font-size: 3vmin;
    margin-right: 1vmin;
}

#task-due-date {
    text-align: center;
}

.errand-entryeven {
    color: white;
}

.errand-entryodd {
    color: blue;
    // text-stroke: 1px white;
    // -webkit-text-stroke: 1px white;
}

.errand-entryodd, .errand-entryeven {
    display: flex;
    justify-content: space-between;
    padding: 0 3vmin;
    font-family: 'Heebo', sans-serif;
    // font-family: 'Kanit', sans-serif;
    font-weight: bold;
    animation: showCover 1s ease-out;
}

.outp {
    display: flex;
    flex-direction: column;
    width: 50vmin;
    justify-content: center;
    align-items: center;
    text-align: center;
    overflow-y: scroll;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    font-size: 3vmin;
    padding: 0 2vmin;
    line-height: 0.3vmin;
}

.outp::-webkit-scrollbar {
    display: none;
}

.delete-button {
    background: linear-gradient(rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.1));
    padding: 0.5vmin;
    font-weight: bold;
    font-size: 2vmin;
}

.delete {
    visibility: hidden;
}

.delete:hover {
    visibility: visible;
}

@keyframes showCover {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}