$numRows: 6;
$row-width: 100/$numRows;

#budget-body {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: radial-gradient(#9E7676 27%, #815B5B 82%);
    font-family: 'Aboreto', cursive;
    overflow: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
    animation: showCover 1s ease-out;
}

@keyframes showCover {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}


.project-title {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-self: center;
    font-family: 'Alkalami', serif;
    font-size: 4vmin;
    text-transform: uppercase;
    margin: unset;
    padding-top: 2vh;
    color: #594545;
}

.no-budgin-input {
    width: 12vmin;
    text-align: center;
    background-color: unset;
    border-bottom: 0.2rem solid #FFF8EA;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    padding: 0.1rem 0.3rem;
}

select option {
    background: rgba(0, 0, 0, 0.5);
}

#expense-category-select {
    margin: 0 1vmin;
    background: rgba(0, 0, 0, 0.1);
    text-transform: uppercase;
    font-size: 2vmin;
    font-weight: bold;
    text-align: center;
    border: inset 2px #FFF8EA;
}

#expense-category-select,
#expense-entry-button {
    border-radius: 3px;
    color: #FFF8EA;
}

#expense-entry-button {
    background: rgba(0, 0, 0, 0.1);
    border: 3px double #FFF8EA;
    padding: 0.5vmin 1vmin;
    transition: all 1s ease-out;
    font-family: 'Irish Grover', cursive;
    margin: 0 1vmin;
    font-size: 3vmin;
}

#expense-entry-button:active {
    scale: calc(0.75);
    color: rgb(212, 0, 0);
    font-weight: bolder;
}

#budget-amount {
    color: rgba(1, 162, 1, 1);
    transition: opacity 1s ease-out;
}

.budget-caption {
    margin: 0;
    text-align: center;
    font-size: 1rem;
    color: #FFF8EA;
}

#remainder {
    color: rgb(230, 3, 3);
}

#budget-input,
#expense-item-input-field,
#expense-amount-input-field,
#budget-input::placeholder,
#expense-item-input-field::placeholder,
#expense-amount-input-field::placeholder {
    color: #FFF8EA;
    // font-family: 'Aboreto', cursive;
    text-transform: uppercase;
    font-family: 'Volkhov', serif;
    font-size: 2vmin;
}

#expense-item-input-field {
    width: 18vmin;
}

#input-form {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1px 0 1px 9vmin;
    color: #FFF8EA;
}

#budget-form {
    width: 15vmin;
}

#budget-information-panel {
    display: flex;
    padding: 0.5vmin 4vmin;
    margin: 3vmin auto;
}

#expense-form {
    display: flex;
    flex-wrap: wrap;
    width: 65vmin;
    align-items: center;
    margin-left: 2vmin;
}

.current-variable-info {
    margin: 1vmin 3vmin;
    text-align: center;
    font-family: "Viaoda Libre", cursive; 
    font-weight: bold;
    font-size: 3.6vmin;
    text-shadow: -6px 6px 27px #594545, 6px -6px 27px #594545;
    animation: openVariableSummary 2s ease-out forwards;
}

#expense-total, .expense-item {
    color: rgba(255, 230, 0, 0.7);
}

#expense-category-summary {
    text-shadow: -1px 1px 9px #FFF8EA, 1px -1px 9px #FFF8EA;
    animation: slideFromTop 1s ease;
}

#expense-content {
    display: flex;
    flex-direction: column;
    width: max-content;
    // height: auto;
    line-height: 7vh;
    align-items: center;
    justify-content: space-between;
    animation: openOverview 1s ease;
    padding-bottom: 2vmin;
    border-bottom: 0.2rem solid #FFF8EA;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    // overflow: hidden;
}

.expense-category-list {
    display: flex;
    // justify-content: space-between;
}

.expense-category-item {
    animation: openCategorySummary 2s ease-out forwards;
}

@keyframes openVariableSummary {
    0% {
        opacity: 0;
        transform: translateY(-100%);
    }
    50% {
        opacity: 1;
        transform: translateY(20%);
    }
    100% {
        opacity: 1;
        transform: translateY(0%);
    }
}

@keyframes openCategorySummary {
    0% {
        opacity: 0;
        transform: translateY(-100%);
    }
    50% {
        opacity: 0.5;
        transform: translateY(20%);
    }
    100% {
        opacity: 1;
        transform: translateY(0%);
    }
}

@keyframes openOverview {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}

.expense-category-output {
    animation: slideFromLeft 1s ease;
}

#expense-category-summary, .expense-category-output {
    text-transform: uppercase;
    font-family: 'Erica One', cursive;
    font-size: 6vmin;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 9px;
    margin: unset;
    color: #594545;
}

.expense-category-output:hover, #expense-category-summary:hover {
    cursor: pointer;
}

.expense-category-output-total {
    font-family: 'Diplomata SC', cursive;
    font-size: 4vmin;
    animation: slideFromRight 1s ease;
}

@keyframes slideFromLeft {
    0% {
        transform: translateX(-100%);
        opacity: 0.5;
    }   
    50% {
        transform: translateX(10%);
        opacity: 1;
    }
    100% {
        transform: translateX(0%);
    }
}

@keyframes slideFromRight {
    0% {
        transform: translateX(100%);
        opacity: 0.5;
    }   
    50% {
        transform: translateX(-10%);
        opacity: 1;
    }
    100% {
        transform: translateX(0%);
    }
}

@keyframes slideFromTop {
    0% {
        transform: translateY(-100%);
        opacity: 0.5;
    }   
    50% {
        transform: translateY(10%);
        opacity: 1;
    }
    100% {
        transform: translateY(0%);
    }
}

@keyframes slideFromBottom {
    0% {
        transform: translateY(100%);
        opacity: 0.5;
    }   
    50% {
        transform: translateY(-10%);
        opacity: 1;
    }
    100% {
        transform: translateY(0%);
    }
}

.expense-category-output, .expense-category-output-total {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 9px;
    margin: unset;
    color: #594545;
}

.expense-category-item {
    display: flex;
    justify-content: space-between;
    font-size: 3vmin;
    align-items: center;
    font-family: 'Almendra', serif;
    color: #594545;
    text-transform: uppercase;
    margin: unset;
    padding: unset;
    line-height: 0.1vmin;
    animation: slideFromBottom 1s ease;
}

#expense-list {
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

#expense-list::-webkit-scrollbar {
    display: none;
}